* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'ubuntu-regular';
}

body {
  height: 100vh;
  color: var(--tg-theme-text-color);
  background: var(--tg-theme-bg-color);
}

button {
  background: var(--tg-theme-button-color);
  color: var(--tg-theme-button-text-color);
  border: none;
}

#root {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title-form {
  font-size: 18px;
  margin-left: 10px;
}

.block {
  width: 90%;
  margin-top: 10px;
}

.select-input {
  border: 1px solid black;
  border-radius: 4px;
}

.btn {
  width: 50%;
  padding: 15px 0;
  margin-top: 20px;
}

.error-message {
  color: red;
  font-size: 12px;
}

.d-flex {
  display: flex;
}

.gap-5 {
  gap: 5px
}

.container {
  width: 90%;
}

#progress-label {
  height: 40px;
}

.progress-lines {
  display: flex;
  align-items: center;
}